import NProgress from 'nprogress'  //
import 'nprogress/nprogress.css'  //

// 
NProgress.configure({
    easing: 'ease',  //     
    speed: 500,  // 
    showSpinner: true, // icon
    trickleSpeed: 800, //     
    trickRate: 0.02,
    minimum: 0.1 // 
})

// 
export const start = () => {
    NProgress.start()
}

// 
export const close = () => {
    NProgress.done()
}