//main.js vue
//import...from...ES6
import Vue from 'vue'  //vue
import App from '@/App'  //App.vue
import router from '@/router'  //
import store from '@/store'
import '@/plugins'  //  index.js,@/plugins@/plugins/index.js
import 'normalize.css'  //cssmain.js
import { start, close } from '@/utils/nprogress.js'

Vue.config.productionTip = false  // true falsetrue

router.beforeEach(async (to, from, next) => {
  //   
  start()
  next()
})

router.afterEach(() => {
  // 
  close()
})

new Vue({  //Vue
  router,  //router  vue 
  store,  //vuex
  render: h => h(App),  // ,App =>ES6 
}).$mount('#app')  //#apppublicindex.htmlidindex.htmlidapp--el:'#app'